body {
  margin: 0;
  background-color: rgb(255, 255, 255);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.logo{
  font-weight: 700 !important;
  color: rgb(195, 208, 211);
  text-shadow: 0 -1px 9px rgb(0, 0, 0);
  font-size: 1.8rem !important;
  margin-left: .75rem !important;
}
.top-pane {
  background-color: hsl(225, 6%, 25%);
}
.pane {
  height: 50vh;
  display: flex;
}
.pane:nth-child(1) {
  resize: vertical;
  overflow: hidden;
}
.pane:nth-child(2) {
  height: 95vh;
}
.editor-container {
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: .5rem;
  background-color: hsl(225, 6%, 25%);

}

.editor-container.collapsed {
  flex-grow: 0;
}

.editor-container.collapsed .CodeMirror-scroll {
  position: absolute;
  overflow: hidden !important;
}

.expend-collapse-btn {
  margin-left: .5rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

.editor-title {
  display: flex;
  justify-content: space-between;
  color: rgb(184, 184, 184);
  background-color: hsl(225, 6%, 12%);
  padding: .5rem .5rem .5rem 1.2rem;
  font-weight: 500;
  letter-spacing: .5px;
  border-top-right-radius: .5rem;
  border-top-left-radius: .5rem;
}
.editor-title:hover {
  color: rgb(233, 233, 233);
  font-weight: 600;
  transition: .3s ease-in-out;
  cursor: crosshair;
}
.CodeMirror {
  height: 100% !important;
}

.code-mirror-wrapper {
  flex-grow: 1;
  border-bottom-left-radius: .5rem;
  border-bottom-right-radius: .5rem;
  overflow: hidden;
}

::-webkit-resizer {
  background: rgb(150, 211, 193);
  box-shadow: 0px 0px 4px 3px white;
  border: 3px solid gray;
  border-style: outset;
  border-radius: 50% 50% 0 0 ;
}
body::-webkit-scrollbar {
  width: .9em;
  background-color: rgb(88, 141, 202);
}
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  
}
body::-webkit-scrollbar-thumb {
  background-color: rgb(221, 158, 107);
  border-radius: 15px;

}

@media (min-width: 600px) { 
.MuiContainer-root {
    padding-left: 0px !important;
    padding-right: 0px !important;
} }
.MuiContainer-root {
    padding-left: 0px !important;
    padding-right: 0px !important;
}

.MuiAppBar-colorPrimary {
  background-image: linear-gradient(140deg, #4f8cdb 0%, #2e278f 50%, #EADEDB 75%) !important;
}

@media (min-width: 600px) {
.MuiToolbar-regular {
    min-height: 50px !important;
}
}
@media (min-width: 0px) and (orientation: landscape){
  .MuiToolbar-regular {
    min-height: 34px !important;
}
}

.MuiToolbar-regular {
    min-height: 42px !important;
}